.header {
  position: relative;

  display: grid;

  min-height: 100vh;
  padding: 100px 0;

  color: $light-color;
  background-color: $base-color;
  background-position: 50% 50%;
  background-size: cover;

  align-content: center;

  &-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    opacity: 0.8;
    background-color: $base-color;
  }

  &-content {
    z-index: 1;
  }

  &-title {
    margin: 0 0 53px;

    text-align: center;
  }

  &-buttons {
    text-align: center;

    .btn {
      display: inline-block;
    }
  }
}

.header-menu {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;

  justify-content: center;

  @media (max-width: $small) {
    background-color: rgba($grey-dark, 0.25);
  }

  .container {
    grid-template-columns: 200px 1fr;
  }
}

.header-logo {
  letter-spacing: 0.5em;

  align-self: center;
  a {
    display: block;

    width: auto;
    padding: $gutter $gutter $gutter 0;

    &:hover,
    &:focus {
      color: $light-color;
    }
  }

  span {
    display: inline-block;

    padding: 15px 7px 15px 15px;

    border: 2px solid $highlight-color;
  }
}

.header-navbar {
  position: relative;

  display: flex;

  align-self: center;
  justify-content: flex-end;
}

.header-navbar__list {
  margin: 0 (-$gutter) 0 0;
  padding: 0;

  list-style-type: none;

  @media (min-width: $small + 1) {
    display: flex;
  }

  @media (max-width: $small) {
    display: none;
  }

  &.active {
    position: absolute;
    top: 100%;

    display: flex;
    flex-direction: column;

    text-align: right;

    background-color: rgba($grey-dark, 0.5);
  }
}

.header-navbar__hamburger {
  position: relative;

  width: 30px;
  height: 23px;
  margin: $gutter 0;

  cursor: pointer;
  transition: 0.5s ease-in-out;
  transform: rotate(0deg);

  @media (min-width: $small + 1) {
    display: none;
  }

  @media (max-width: $small) {
    display: block;
  }

  span {
    position: absolute;
    left: 0;

    display: block;

    width: 100%;
    height: 3px;

    transition: 0.25s ease-in-out;
    transform: rotate(0deg);

    opacity: 1;
    border-radius: 3px;
    background: $light-color;

    &:nth-of-type(1) {
      top: 0;
    }

    &:nth-of-type(2) {
      top: 9px;
    }

    &:nth-of-type(3) {
      top: 18px;
    }
  }

  &.active span {
    &:nth-of-type(1) {
      top: 9px;

      transform: rotate(135deg);
    }

    &:nth-of-type(2) {
      left: -30px;

      opacity: 0;
    }

    &:nth-of-type(3) {
      top: 9px;

      transform: rotate(-135deg);
    }
  }
}

.header-navbar__item {
  a {
    display: block;

    padding: $gutter;

    transition: color 0.5s;

    color: $light-color;

    &:hover,
    &:focus {
      color: $accent-color;
    }
  }
}
